.product-page-container {
  // position: absolute;
  // top: 12vh;

  margin-top: 12vh;
  // height: 100%;
  width: 100vw;
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 1050px) {
    overflow-y: scroll;
    overflow-x: hidden;
  }
  @media screen and (min-width: 1050px) {
    .prod-top-contain {
      width: 100vw;
      padding-left: 60px;
      padding-right: 60px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .conText {
        font-weight: 600;
        padding-top: 10px;
        font-family: "Montserrat";
        font-weight: 500;
      }
      .blink {
        animation: blinker 1.5s linear infinite;
        @keyframes blinker {
          50% {
            opacity: 0;
          }
        }
      }
    }

    .mob-prod-container {
      display: none;
    }

    .prod-container {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100vw;
      height: 70vh;
      padding-right: 60px;
      padding-left: 60px;
      .prod-info-container {
        display: flex;
        flex-direction: column;
        font-size: large;
        font-weight: 300;
        align-self: center;
        width: 20%;
        span {
          margin: 10px;
        }
      }
      .prod-img-container {
        margin-top: 20px;
        font-size: 35px;
        font-weight: 600;
        display: flex;
        flex-direction: column;
        justify-content: center;
        // opacity: 0.8;
        // align-items: center;
        width: 58%;
        .prod-img-text-container {
          font-family: proxima-nova, sans-serif;
          letter-spacing: 0;
          font-weight: 700;
          font-size: 40px;
        }
        .swiper-scrollbar {
          height: 3px;
        }
        .swiper-scrollbar-drag {
          background: black;
          height: 100%;
        }
        .shoeImg {
          .swiper-container {
            height: 90%;
            width: 100%;
          }
          .swiper-slide {
            /* Center slide text vertically */
            display: -webkit-box;
            display: -ms-flexbox;
            display: -webkit-flex;
            display: flex;
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            -webkit-justify-content: center;
            justify-content: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            -webkit-align-items: center;
            align-items: center;
            img {
              max-width: 100%;
              max-height: 100%;
            }
          }
        }
      }
      .prod-size-container {
        width: 18%;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        span {
          padding-top: 15px;
          font-weight: 600;
          min-width: fit-content;
        }
      }
      @media screen and (max-width: 1050px) {
        display: none;
      }
    }
    .prod-page-suggest {
      width: 100%;
      justify-content: center;
      align-items: center;
      align-self: center;
    }
    .prod-text {
      width: 50%;
      justify-content: center;
      align-items: center;
      align-self: center;
      text-align: center;
      margin-top: 10px;
    }

    .customFont {
      font-family: "Montserrat";
      font-weight: 350;
    }
  }

  @media screen and (min-width: 0px) and (max-width: 481px) {
    margin-top: 10px;
    .prod-top-contain {
      display: none;
    }
    .prod-container {
      display: none;
    }
    .prod-text {
      justify-content: center;
      align-items: center;
      align-self: center;
      text-align: left;
      margin-top: 10px;
      padding-left: 25px;
      padding-right: 25px;
    }

    .mob-prod-container {
      .prod-img-container {
        margin-top: 20px;
        font-size: 35px;
        font-weight: 600;
        display: flex;
        flex-direction: column;
        justify-content: center;
        // opacity: 0.8;
        // align-items: center;
        width: 100%;
        height: 45vh;
        .prod-img-text-container {
          font-family: proxima-nova, sans-serif;
          letter-spacing: 0;
          font-weight: 700;
          font-size: 40px;
        }
        .swiper-scrollbar {
          height: 3px;
          margin-left: 5%;
          margin-right: 5%;
          width: 90%;
          left: 0;
        }
        .swiper-scrollbar-drag {
          background: black;
          height: 100%;
        }
        .shoeImg {
          .swiper-container {
            height: 90%;
            width: 100%;
          }
          .swiper-slide {
            /* Center slide text vertically */
            display: -webkit-box;
            display: -ms-flexbox;
            display: -webkit-flex;
            display: flex;
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            -webkit-justify-content: center;
            justify-content: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            -webkit-align-items: center;
            align-items: center;
          }
        }
        // img {
        //   max-width: 100%;
        //   max-height: 100%;
        // }
      }

      .mob-prod-top-contain {
        width: 100vw;
        padding-left: 15px;
        padding-right: 15px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        .conText {
          font-family: "Montserrat";
          font-size: small;
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
        }
        .blink {
          animation: blinker 1.5s linear infinite;
          @keyframes blinker {
            50% {
              opacity: 0;
            }
          }
        }
      }

      .mob-prod-img-container {
        display: flex;
        width: 100%;
        height: 40vh;
        position: relative;
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;
        transform: scaleX(-1);
        filter: FlipH;
      }

      .prod-info-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 1rem;
        font-weight: 400;
        align-self: center;
        .prod-price-container {
          font-size: 1.5rem;
        }
      }
    }
    .prod-page-suggest {
      width: 100%;
      justify-content: center;
      align-items: center;
      align-self: center;

      .suggestions {
        margin: 0px;
        .sug-txt {
          margin: 0px;
          margin-top: 20px;
          padding-left: 22px;
          font-size: larger;
        }
      }

      .card {
      }
    }
    // .prod-text {
    //   width: 50%;
    //   justify-content: center;
    //   align-items: center;
    //   align-self: center;
    //   text-align: center;
    //   margin-top: 10px;
    // }
    .customFont {
      font-family: "Montserrat";
      font-weight: 100;
    }
  }
}
